:root {
  --primary-light: #aa0601;
  --primary-main: #650300;
  --primary-dark: #8c0d0a;
  --secondary: #fbf2f2;
  --default: #707070;
  --light-default: #dcdcdc;
}
body{
  font-family: 'Roboto', 'Noto Sans TC', sans-serif;
}
a {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  line-height: 1.5;
}

h1 {
  font-size: 72px;
}

h2 {
  font-size: 64px;
}

h3 {
  font-size: 48px;
}

h4 {
  font-size: 36px;
}

h5 {
  font-size: 24px;
}

h6 {
  font-size: 20px;
}

p {
  font-size: 16px;
}

.color-primary-main {
  color: var(--primary-main);
}

.color-primary-light {
  color: var(--primary-light);
}

.color-secondary {
  color: var(--secondary);
}

.color-default {
  color: var(--default);
}

.color-0 {
  color: #000;
}

.color-f {
  color: #fff;
}

.fz-sm {
  font-size: 14px;
}

.font-bold {
  font-weight: 700;
}

.font-italic{
  font-style:italic;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.container-sm {
  max-width: 730px;
  margin: 0 auto;
}

@media (min-width: 768px) and (max-width: 979px) {
  h1 {
    font-size: 48px;
  }

  h2 {
    font-size: 48px;
  }

  h3 {
    font-size: 32px;
  }

  h4 {
    font-size: 28px;
  }

  h5 {
    font-size: 24px;
  }

  h6 {
    font-size: 20px;
  }

  p {
    font-size: 16px;
  }
  .container {
    margin-left: 40px;
    margin-right: 40px;
  }
  .container-sm {
    width: 450px;
  }
}

@media (max-width: 767px) {
  h1 {
    font-size: 32px;
  }

  h2 {
    font-size: 32px;
  }

  h3 {
    font-size: 24px;
  }

  h4 {
    font-size: 24px;
  }

  h5 {
    font-size: 20px;
  }

  h6 {
    font-size: 16px;
  }

  p {
    font-size: 16px;
  }
  .container {
    margin-left: 15px;
    margin-right: 15px;
  }
  .container-sm {
    width: auto;
    padding: 0 15px;
  }
}
